<template>
    <div id="secure">
        <h1>Secure Area</h1>
        <p>
            This is a secure area
        </p>
    </div>
</template>

<script>
    export default {
        name: 'UserPage',
        data() {
            return {};
        },
        created() {
        if (!sessionStorage.getItem('token')) {
            this.$router.replace({ path: "login" });
            this.$router.go(0);
        }
    }
    }
</script>

<style scoped>
    #secure {
        background-color: #FFFFFF;
